header {
  height: auto;
  width: 100%;
  background-color: black;
  height: 80px;

  position: relative;
  z-index: 3;

  display: flex;
  justify-content: center;
  align-items: center;

  padding-left: 5%;
  padding-right: 5%;

  .header-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
  }

  svg {
    width: 120px;
    fill: white;
    margin-right: 2px;
  }
}

@media only screen and (max-width: 990px) {
  header {
    height: 54px;
  }
}


@media only screen and (max-width: 990px) {
  svg {
    width: 88.5px;
  }
}

.desktop-nav {
  display: flex;
}

@media only screen and (max-width: 990px) {
  .desktop-nav {
    display: none;
  }
}

.desktop-nav a {
  color: white;
  padding: 28px;
  font-weight: bold;
  font-size: 1.25em;
  letter-spacing: 0.5px;
  display: inline;
  cursor: pointer;
  font-size: 16px;
}

header span {
  display: flex;
  align-items: center;
}

.logo-wrap {
  display: flex;
  align-items: center;
  color: white;

  p {
    font-size: 22px;
    font-family: "circularLight";

    @media only screen and (max-width: 990px) {
      font-size: 17px;
      font-weight: bold;
    }
  }
}

.mobile-menu {
  color: white;
  font-size: 22px;

  @media only screen and (min-width: 990px) {
    display: none;
  }
}

.mobile-nav-container{
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0,0.5);
  z-index: 400;
}

.mobile-nav {
  padding-top: 5%;
  padding-left: 5%;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  width: 80%;
  height: 100%;
  background-color: black;
  z-index: 500;
  display: none;
  align-items: flex-start;



  i{
    color:white;
    font-size: 22px;
  }


  a {
    color: white;
    font-size: 2em;
    margin-top: 30px;
  }
}


